
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters } from 'vuex'
import { cardnum } from 'api/api'
export default {
name: 'tarbar',
data() {
return {
cartnum: '',
}
},
created() {
this.getCardnum()
},
computed: {
...mapGetters([
'userAccount'
])
},
methods: {
getCardnum() {
let token = this.userAccount.token
let uid = this.userAccount.uid
cardnum(token,uid).then((res)=>{
if(res.status === 1) {
if(res.data.num === 0){
this.cartnum = ''
}else{
this.cartnum = res.data.num + ''
}
}
})
},
index () {
this.$router.push({
path: `/index/`
})
},
category () {
this.$router.push({
path: `/category/`
})
},
shopcart () {
this.$router.push({
path: `/shopcart/`
})
},
me () {
this.$router.push({
path: `/me/`
})
}
},
components: {
Tabbar,
TabbarItem
}
}



