 import { getListCard, addGoodToCard, reduceGoodToCard, cardGoodClick } from 'api/api'
 import Scroll from 'base/scroll/scroll'
 import Tarbar from 'base/tarbar/tarbarthree'
 import { mapGetters } from 'vuex'
 export default {
   data () {
     return {
       cartList: [],
       status: true,
       allsum: 0,
       allnum: 0
     }
   },
   created() {
     this._getListCard()
   },
   computed: {
    ...mapGetters([
			'userAccount'
		])
   },
   methods: {
    back () {
       this.$router.back()
    },
    shoptrue(item){
      this.cartList.forEach((pro)=>{
        pro.selected === '0' ? this.choosetrue(item):''//循环列表商品，先筛选出目前没选中的商品，给它执行choosetrue函数
      })
    },
    shopfalse(item){
      this.cartList.forEach((pro)=>{
        pro.selected === '1' ? this.choosefalse(item):''//循环列表商品，先筛选出目前被选中的商品，给它执行choosefalse函数
      })
    },
    cartchoose() {
      // 进行取消全选操作,同时进行操作接口操作
      this.status = !this.status
      if(this.status) {
        this.chooseall()
      }else{
        this.cacelchooseall()
      }
    },
    clearCart() {
       this.allsum = 0
       this.allnum = 0
    },
    choosetrue(item) {
      // console.log(123)
      item.selected = '1' // 将商品选中状态改为true
      //这里执行了两部，选中商品数量先+1，再与该店铺商品数量比较，如果相等就更改店铺选中状态为true
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let select = 1
      let all = 0
      let cardid = item.id
      cardGoodClick(uid,token,select,all,cardid).then((res)=>{
        if(res.status === 1) {
          this._getListCard()
        }
      })
    },
    chooseall() {
      // 全选操作
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let select = 1
      let all = 1
      let cardid = 0
      cardGoodClick(uid,token,select,all,cardid).then((res)=>{
        if(res.status === 1) {
          this._getListCard()
        }
      })
    },
    cacelchooseall() {
      // 取消全选操作
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let select = 0
      let all = 1
      let cardid = 0
      cardGoodClick(uid,token,select,all,cardid).then((res)=>{
        if(res.status === 1) {
          this._getListCard()
        }
      })
    },
    choosefalse(item) {
      item.selected = '0' // 将商品选中状态改为false
      // 同时进行购物车减少操作
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let select = 0
      let all = 0
      let cardid = item.id
      cardGoodClick(uid,token,select,all,cardid).then((res)=>{
        if(res.status === 1) {
          this._getListCard()
        }
      })
    },
    choose(item){
      item.selected === '0' ? this.choosetrue(item) : this.choosefalse(item)
    },
    _getListCard() {
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      getListCard(uid,token).then((res) => {
        this.cartList = res.data.data || []
        let selectedArr = []
        let selectArr = []
        this.cartList.forEach((item)=>{
          if(item.selected === '0') {
            selectArr.push(item)
          }else{
            selectedArr.push(item)
          }
        })
        if (selectedArr.length === this.cartList.length) {
          this.status = true
        }else{
          this.status = false
        }
        this.allsum = res.data.allsum
        this.allnum = res.data.allcount
      })
    },
    addcart(item) {
      let num = 1
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let group_id = item.group_id
      addGoodToCard(uid,token,group_id,num).then((res)=>{
        if(res.status === 1) {
          // 重新获取订单列表 + 订单数量
          this._getListCard()
        }
      })
    },
    reducecart(item) {
      if(item.pro_num == '1' ) {
        return false
      }
      let num = 1
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let group_id = item.group_id
      reduceGoodToCard(uid,token,group_id,num).then((res)=>{
        if(res.status === 1) {
          // 重新获取订单列表 + 订单数量
          this._getListCard()
        }
      })
    },
    toOrder() {
      this.$router.push({
        path: '/order'
      })
    }
  },
  components: {
    Tarbar,
    Scroll
  }
 }



